<template>
  <div class="employee-expense-container">
    <el-card>
      <div slot="header" class="clearfix">
        <span>员工费用管理</span>
        <el-button type="primary" size="small" @click="handleAdd" style="float: right">新增费用</el-button>
      </div>

      <!-- 查询条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="费用名称">
          <el-input v-model="searchForm.remark" placeholder="请输入费用名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
      <!-- 表格数据 -->
      <el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column prop="employeeName" label="员工姓名"></el-table-column>
        <el-table-column prop="remark" label="费用名称"></el-table-column>
        <el-table-column prop="amount" label="费用金额">
          <template slot-scope="scope">
            {{ scope.row.amount.toFixed(2) }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-tag :type="scope.row.status === 1 ? 'success' : 'warning'">
              {{ scope.row.status === 1 ? '已报销' : '待报销' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          background
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
      <!-- 批量操作 -->
      <div v-if="selectedRows.length > 0" class="batch-operation">
        <el-button type="danger" @click="handleBatchDelete">批量删除</el-button>
        <el-button type="success" @click="handleBatchReimburse">批量报销</el-button>
      </div>
    </el-card>
    <!-- 费用表单对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="60%">
      <el-form :model="form" :rules="rules" ref="employeeExpenseForm" label-width="100px">
        <el-row :gutter="20">
          <el-col :span="24">
            <el-form-item label="员工" prop="employeeId">
              <el-select style="width: 100%;" v-model="form.employeeId" placeholder="请选择员工">
                <el-option v-for="employee in employeeList" :key="employee.id" :label="employee.employeeName" :value="employee.id"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="费用名称" prop="remark">
              <el-input v-model="form.remark" placeholder="请输入费用名称"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="费用金额" prop="amount">
              <el-input v-model.number="form.amount" placeholder="请输入费用金额"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="状态" prop="status">
              <el-switch v-model="form.status" active-value="1" inactive-value="0"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { employeeExpenseAPI, employeeAPI } from '../api/api.js'

export default {
  name: 'EmployeeExpense',
  data() {
    return {
      // 查询条件
      searchForm: {
        expenseName: '',
        status: ''
      },
      // 表格数据
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      // 选中的行
      selectedRows: [],
      // 对话框
      dialogVisible: false,
      dialogTitle: '',
      // 表单数据
      form: {
        id: null,
        expenseName: '',
        amount: 0,
        status: 0,
        employeeId: null,
        companyId: 1
      },
      // 表单验证规则
      rules: {
        employeeId: [
          { required: true, message: '请选择员工', trigger: 'change' }
        ],
        remark: [
          { required: true, message: '费用名称不能为空', trigger: 'blur' }
        ],
        amount: [
          { required: true, message: '费用金额不能为空', trigger: 'blur' },
          { type: 'number', min: 0.01, message: '费用金额必须大于0', trigger: 'blur' }
        ]
      }
    }
  },
  mounted() {
    this.loadEmployees()
    this.loadData()
  },
  methods: {
    // 加载员工列表
    loadEmployees() {
      // 获取激活状态的员工列表
      const params = {
        pageNum: 1,
        pageSize: 100, // 假设最多100个员工
        status: 1 // 激活状态
      }
      employeeAPI.page(params).then(res => {
        if (res.code === 200) {
          this.employeeList = res.data.records
        } else {
          this.$message.error(res.message)
        }
      })
    },

    // 加载数据
    loadData() {
      const params = {
        pageNum: this.currentPage,
        pageSize: this.pageSize,
        ...this.searchForm
      }

      employeeExpenseAPI.getList(params).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.records
          this.total = res.data.total
          this.tableData.forEach(order => {
            const employee = this.employeeList.find(s => s.id === order.employeeId)
            if (employee) {
              order.employeeName = employee.employeeName
            }
          })
        } else {
          this.$message.error(res.message)
        }
      }).catch(err => {
        this.$message.error('加载数据失败')
      })
    },

    // 查询
    handleSearch() {
      this.currentPage = 1
      this.loadData()
    },

    // 重置
    handleReset() {
      this.searchForm = {
        expenseName: '',
        status: '',
        employeeId: ''
      }
      this.loadData()
    },

    // 分页大小改变
    handleSizeChange(size) {
      this.pageSize = size
      this.loadData()
    },

    // 当前页改变
    handleCurrentChange(current) {
      this.currentPage = current
      this.loadData()
    },

    // 选择行变化
    handleSelectionChange(rows) {
      this.selectedRows = rows
    },

    // 新增
    handleAdd() {
      this.dialogTitle = '新增费用'
      this.form = {
        id: null,
        expenseName: '',
        amount: 0,
        status: 0,
        employeeId: null,
        companyId: 1
      }
      this.dialogVisible = true
    },

    // 编辑
    handleEdit(row) {
      this.dialogTitle = '编辑费用'
      employeeExpenseAPI.getDetail(row.id).then(res => {
        if (res.code === 200) {
          this.form = {
            ...res.data
          }
          this.dialogVisible = true
        } else {
          this.$message.error(res.message)
        }
      })
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除该费用吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        employeeExpenseAPI.delete(id).then(res => {
          if (res.code === 200) {
            this.$message.success('删除成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('删除失败')
        })
      })
    },

    // 批量删除
    handleBatchDelete() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要删除选中的 ${ids.length} 个费用吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 批量删除逻辑
        // 这里简化处理，实际应该调用批量删除接口
        ids.forEach(id => {
          employeeExpenseAPI.delete(id).then(res => {
            if (res.code !== 200) {
              this.$message.error(res.message)
            }
          })
        })
        this.$message.success('批量删除成功')
        this.loadData()
        this.selectedRows = []
      })
    },

    // 批量报销
    handleBatchReimburse() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要报销选中的 ${ids.length} 个费用吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 批量报销逻辑
        ids.forEach(id => {
          employeeExpenseAPI.approve(id).then(res => {
            if (res.code !== 200) {
              this.$message.error(res.message)
            }
          })
        })
        this.$message.success('批量报销成功')
        this.loadData()
        this.selectedRows = []
      })
    },

    // 取消对话框
    handleCancel() {
      this.dialogVisible = false
      this.$refs.employeeExpenseForm.resetFields()
    },

    // 提交表单
    handleSubmit() {
      this.$refs.employeeExpenseForm.validate((valid) => {
        if (valid) {
          if (this.form.id) {
            // 编辑
            employeeExpenseAPI.update(this.form).then(res => {
              if (res.code === 200) {
                this.$message.success('更新成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('更新失败')
            })
          } else {
            // 新增
            employeeExpenseAPI.add(this.form).then(res => {
              if (res.code === 200) {
                this.$message.success('新增成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('新增失败')
            })
          }
        }
      })
    }
  }
}
</script>

<style scoped>
.employee-expense-container {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  text-align: right;
}

.batch-operation {
  margin-top: 10px;
}
</style>
